.navBar{
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
}

.navBar ul{
    margin: 0;
    padding: 0;
}

.navBar li{
    list-style: none;
    display: inline-block;
}

.navBar li a{
    text-decoration: none;
    display: block;
    font-size: 20px;
    text-transform: capitalize;
    font-weight: 400;
    color: #565673;
    padding: 0 10px;
}

.navBarSearch form{
    display: flex;
    padding: 0 10px;
}

.navBarSearchInput{
    width: 0;
    border: 0;
    transition: width 0.5s ease-in;
}

.navBarSearchInput:focus{
    outline:none;
}

.navBarSearchInput.active{
    width: 150px;
}

.navBarSearchIcon{
    cursor: pointer;
}